
@import (once) "./global";

#app {
  > .body {
    background: #f7f7f7;
    > .staff {
      margin-top: -44px;

      > .p2r-hidden {
        height: 44px;
        .h-box();
        justify-content: center;
        align-items: center;
        color: #ccc;
      }

      > .person {
        height: 44px;
        .h-box();
        padding-left: 8px;
        background: #fff;
        border-bottom: 1px solid #ccc;
        align-items: center;

        > .age {
          font-size: 80%;
          color: #999;
        }
      }
    }
  }
  > .bottom-bar {
    z-index: 10;
    .h-box();
    height: 44px;
    justify-content: center;
    align-items: center;
    box-shadow: 0 -2px 2px #999;
  }
}



